import WucTab from '@/components/wuc-tab/wuc-tab.vue';
import { getProjects , getUserInfo} from '@/api/huizis.js';
export default {
	data() {
		return {
			TabCur: 0,
			tabList: [{ name: '我是会员', icon: 'cuIcon-comment' }, { name: '我是会头', icon: 'cuIcon-comment' }],
			listData: [],
			userInfo:{},
			isHuiItem: true
		};
	},
	components: { WucTab },
	methods: {
		tabChange(index) {
			if (index == 0) {
				this.isHuiItem = true;
				return;
			}
			this.isHuiItem = false;
		},
		toShare(id) {
			uni.navigateTo({
				url: '/pages/share/index?projectId='+id +"&inviteName="+this.userInfo.nickName +"&cid="+ this.userInfo.cid
			});
		}
	},
	onLoad() {
		getProjects().then(response => {
			let initData = [];
			let resData = response.data.data;
			let len = response.data.data.length;
			for (let i = 0; i < len; i++) {
				initData[i] = {
					pid: resData[i].pid,
					name: resData[i].name
				};
			}
			this.listData = initData;
		});
		getUserInfo().then(response => {
			let resData = response.data.data;
			let initData ={
				cid: resData.cid,
				headUrl: resData.headUrl,
				mobile: resData.mobile,
				name: resData.name,
				nickName: resData.nickName,
				uid: resData.uid,
			}
			this.userInfo = initData ;
		});
	}
};
